    <script>
    import GoogleMap from '../Modals/GoogleMap.vue';
    import {
        defineComponent,
    } from '@vue/composition-api'
    import {
        extend,
        ValidationObserver,
        ValidationProvider
    } from 'vee-validate';
    import {
        ConvertCustomFieldIntoDynamicFormObj,
        ConvertDynamicFormObjToCustomFieldObj
    } from "../../assets/common/vue-common.js";
    import DataService from '../../services/DataService';
    import moment from 'moment'
import { contains } from 'jquery';
import { ref } from 'vue';
    extend('required', {
        validate(value) {
            console.log("checkk")
            return {
                required: true,
                valid: ['', null, undefined].indexOf(value) === -1,
            };
    
        },
        message: 'The {_field_} is required'
        ///computesRequired: true
    });
    export default defineComponent({
       // name: "Manage Departemts",
        components: {
            ValidationObserver,
            ValidationProvider,
            GoogleMap
        },
        props: {
            addApiUrl: {
                type: String
            },
            emitName: {
                type: String
            },
            moduleName: {
                type: String
            },
            subModule: {
                type: String
            },
            title: {
                type: String
            },
            recordId: {
                type: Number
            },
            addUserGuide:{
                type: String
            },
            refId:{
                type: Number
            },
            callbackfunction: {
                type: Function,
                required:false
        }
        },
        data() {
            return {
                startTime: null,
                endTime: null,
                hoursDifference: null,  
                starRatings: {},  
                visibilitySettings: {},
                fileData:[],              
                editor: this.CLASSIC_EDITOR,
                department: {
                    name: '',
                    status: {
                        name: "active",
                        id: 1
                    },
                    visibaleToclients: false,
                    visibaleToTicting: false,
                    description: '',
                },
                buttons: [{
                    type: "button",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,
                    onClick: this.Save
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closePopup,
                },
                ],
                FormSchema: [],
                OtherSchema:[],
                selectAll: false,
                isLoading: false,
                showContractDiv:false,
                ManageQuestionaire:false,
                modelStars:[],
                GetCheckedValue:null,   
                modelQuestionVisibility:[],
                GetReviewQuestionList:[],
                RadioOptions:[{label:'Enable',value:1},{label:'Disable',value:0}],
                RadioVisibilityOtions:[{label:'Both',value:0},{label:'Candidate',value:1},{label:'Reviewer',value:2}],
                selectedItems: [],
                FeedbackColumnArray:[{FeedbackQuestion:"",FeedbackAnswerType:"",FeedbackIndex:0,FeedbackAnsweringType:'',FeedbackAnsweringValue:''}],
                count:0,
                skill:'',
                notes:'',
                feedback:'',
                goal:'',
                MandatoryString: this.$t('MandatoryString'),
                training:'',
                male:'',
                female:'',
                FeedbackAnsweringValue:'',
                otherFeedBackData:[],
                geofenceEnabled: 'No',
                geofenceType: 'Draw',
                radiusValue: null,
                latitude: 0,
                longitude: 0,
                polygencoordinates:null,
                isEditMode: false,    
                isEditModeInitialized: false,
            }
    
        },
        created: function () {
            let id = this.recordId ? this.recordId : 0
            if(id > 0){
                this.isEditMode = true;
            }
            if (this.moduleName == 'Review' && this.subModule == 'PerformanceReview'){
                 this.GetReviewTemplateData();  
                 this.title = this.$t('Manage') + ' ' + this.$t('ReviewTemplate');             
            }   

            setTimeout(() => {
                this.getchecked();
            }, 2000);
            this.getForm(id)    
        },     
        watch: {
            
            hoursDifference() {
            },    
            
        },
        methods: {

            reloadMap() {
                const geofenceEnabled= this.$refs.dynamicForm.getPropertyValue("enable_geofence", 'value');
                const geofenceType = this.$refs.dynamicForm.getPropertyValue("geofence_type", 'value');
                const radiusValue = this.$refs.dynamicForm.getPropertyValue("radius_meters", 'value');
                let lat = this.$refs.dynamicForm.getPropertyValue("latitude", 'value');
                let lng = this.$refs.dynamicForm.getPropertyValue("longitude", 'value');
                this.$refs.googleMap.reloadMap(geofenceEnabled, geofenceType,radiusValue,lat,lng);
            },
            getchecked(){
            if (this.moduleName == 'Review' && this.subModule == 'PerformanceReview'){       
                if(this.skill != ''){                    
                    $('.input-group').find('.custom-checkbox').find($('input[type="checkbox"]#is_skill_enable_3')).prop('checked',true);
                 }

                 if(this.notes != ''){                    
                    $('.input-group').find('.custom-checkbox').find($('input[type="checkbox"]#is_skill_enable_0')).prop('checked',true);
                 }

                 if(this.feedback != ''){                    
                    $('.input-group').find('.custom-checkbox').find($('input[type="checkbox"]#is_skill_enable_1')).prop('checked',true);
                 }

                 if(this.goal != ''){                    
                    $('.input-group').find('.custom-checkbox').find($('input[type="checkbox"]#is_skill_enable_2')).prop('checked',true);
                 }

                 if(this.training != ''){                    
                    $('.input-group').find('.custom-checkbox').find($('input[type="checkbox"]#is_skill_enable_4')).prop('checked',true);
                 }
                 
              }
              
              if (this.moduleName == 'Configuration' && this.subModule == 'Passport'){  
                if(this.male != ''){                                       
                    $('.input-group').find('.custom-radio').find($('input[type="radio"]#sex_0')).prop('checked',true);
                 }
                 if(this.female != ''){                    
                    $('.input-group').find('.custom-radio').find($('input[type="radio"]#sex_1')).prop('checked',true);
                 }
              }
            },
          getDifference() {
            if (this.startTime && this.endTime) {
                const toMinutes = (timeStr) => {
                    try {
                        timeStr = timeStr.trim().toUpperCase();

                        // Match 12-hour time like "9:00 AM", "12:15 PM", "6 PM", etc.
                        const match = timeStr.match(/^(\d{1,2})(?::(\d{2}))?\s*(AM|PM)$/);

                        if (!match) return NaN;

                        let [_, hourStr, minuteStr, period] = match;
                        let hours = parseInt(hourStr);
                        let minutes = parseInt(minuteStr || '0');

                        if (period === 'PM' && hours !== 12) hours += 12;
                        if (period === 'AM' && hours === 12) hours = 0;

                        return hours * 60 + minutes;
                    } catch (err) {
                        return NaN;
                    }
                };

                const startMinutes = toMinutes(this.startTime);
                const endMinutes = toMinutes(this.endTime);

                if (isNaN(startMinutes) || isNaN(endMinutes)) {
                    this.hoursDifference = 0;
                    return 'Invalid time format.';
                }

                let diffMinutes = endMinutes - startMinutes;
                if (diffMinutes < 0) diffMinutes += 1440; // Handle overnight

                const hours = Math.floor(diffMinutes / 60);
                const minutes = diffMinutes % 60;

                this.hoursDifference = parseFloat((hours + minutes / 60).toFixed(2)); // decimal hours
            } else {
                this.hoursDifference = 0;
                return 'Start and end time are required.';
            }
          },
            GetPassportAttachments: async function (id) {
                var url = `recordId=${id}`;
                try {
                    const response = await DataService.GetPassportAttachments(url);
                    if (response.data.data.length > 0) {
                        var attachments = response.data.data[0];
                        return attachments;  
                    } else {
                        return []; 
                    }
                } catch (error) {
                    console.error("Error fetching passport attachments", error);
                    return [];
                }
            },
            getForm: async function (id) {
                var ths = this;
                ths.isLoading = true;
                var attachmentsArray = [];
                var url = `recordId=${id}&moduleName=${ths.moduleName}&subModuleCode=${ths.subModule}`;
                const attachments = await ths.GetPassportAttachments(id);
                await DataService.GetDynamicForm(url).then(function (response) {
                    if (response != null && response.data != null) {
                        ths.isMapFeature = response.data.isFsmLocating;
                         if (ths.moduleName == 'Company' && ths.subModule == 'Feedback'){
                            ths.otherFeedBackData=[];
                            ths.otherFeedBackData=response.data.otherData; 
                        }    

                        if (ths.moduleName == 'Company' && ths.subModule == 'Location'){
                            ths.otherFeedBackData=[];
                            if(response.data.Attachment.length > 0){
                                ths.otherFeedBackData=response.data.Attachment; 
                                ths.otherFeedBackData.forEach((item, index) => {
                                    ths.latitude =  parseFloat(item.lat);
                                    ths.longitude =  parseFloat(item.lng);
                                    ths.geofenceEnabled= item.geofenceEnabled;
                                    ths.geofenceType= item.geofenceType;
                                    ths.polygencoordinates = item.coordinates;
                                    ths.radiusValue = item.radiusValue;
                                });
                            }
                            response.data.data.forEach((field, gi) => {
                               if(field.name=='parent_location_id'){
                                    console.log('seleted_optons',field.select_options);
                                    console.log('refId',id);
                                    let opts = field.select_options;

                                // If select_options is a string, parse it
                                if (typeof opts === "string") {
                                    try {
                                        opts = JSON.parse(opts);
                                    } catch (e) {
                                        console.error("JSON parse error:", e);
                                    }
                                }

                                const refId = id 

                                // Now filter
                                 opts = opts.filter(o => String(o.value) !== String(refId));

                                // Assign back
                                field.select_options = opts;

                                
                               }
                            });
                           
                        }  
                          


                        ths.FormSchema = ths.$refs.dynamicForm.ConvertCustomFieldIntoDynamicFormObj(response.data.data);  
                     
                        if(ths.moduleName == 'Company' && ths.subModule == 'CompanyHoliday'){
                            var locationname="";
                            if(response.data.locationData.length>0){
                                 locationname= response.data.locationData[0].value;
                            }
                            ths.FormSchema.push({groupId:null,group_name:"",layoutType:'single',visibility:true,
                            Data:[
                                    {
                                        astype: "SlotField",
                                        label: '',
                                        name: "AvailableClientsLang",
                                        value: "",
                                        visibility: true,
                                        placeholder: "",
                                        config: {
                                            options: [],
                                            showAddIcon: false,
                                        },
                                        validationRules: "",
                                    }
                                ]
                            })
                                            ths.$nextTick(() => {
                ths.$refs.dynamicForm.updateProperty("company_location_id", "value", locationname);
            });
                        } 
                       
                        else if (ths.moduleName == 'Review' && ths.subModule == 'PerformanceReview'){
                            ths.FormSchema.push({
                                groupId:null,
