                                            }
                                        }
                                    }
                                });
                            });
                        }
                        if(ths.moduleName == 'Company' && ths.subModule == 'Break') {
                            if(id>0)
                            {
                                ths.FormSchema.forEach((group, gi) => {
                                     group.Data.forEach((field, fi) => {
                                        if(field.name == "no_of_times") {
                                            if(field.value == null || field.value == "") {
                                                field.value = 0;
                                            }
                                        }
                                  
                                    });
                                });
                               
                            }
