            GetBase64String: function (evt) {
            try {
                var vm = this;
                //alert(vm.apiurl);
                //vm.fileData = [];

                var f = evt[0]; // FileList object
                var reader = new FileReader();
                // Closure to capture the file information.
                reader.onload = (function (theFile) {

                    return function (e) {
                        var binaryData = e.target.result;
                        //Converting Binary Data to base 64
                        var base64StringEncoded = window.btoa(binaryData);
                        //showing file converted to base64

                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);

                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;

                        // var ggg = vm.configData;
                        var fileObj = {
                            attachement_mime: attachemnetMime,
                            fileName: fileName,
                            base64String: base64StringEncoded,
                            fileSize: f.size,
                            contentType: f.type
                        };
                        console.log(fileObj);
                        vm.fileData.push(fileObj);
                    };
                })(f);
                // Read in the image file as a data URL.
                reader.readAsBinaryString(f);
            }
            catch (ex) {
            }
            },

            SizeToMb: function (bytes) {
                var vueObj = this;
                var sizeInBuytes = parseInt(vueObj.allowedPerFileSizeInMb, 10) * 1048576;
                if (bytes > sizeInBuytes) {
                    return -1;
                }
                else {
                    var byesInMB = (bytes / 1024) / 1024;
                    return byesInMB;
                }
            },
        
             GetStateDDL: async function (id,state) {
                var vm = this;
                var CountryId = 0;
                CountryId = id;
                var url = `id=&&moduleName=Location&type=${'STATE'}&search=&refId=` + CountryId;
                try {
                    const response = await DataService.GetDDLData(url);
                    if (response.data && response.data.DATA) {  
                        vm.$refs.dynamicForm.updateProperty("location_state", "config.options", response.data.DATA);
                        if(state != undefined){
                            vm.$refs.dynamicForm.getPropertyValue("location_state", "config.options").forEach((s) => {
                                if (s.first_value == state) {
                                    vm.$refs.dynamicForm.updateProperty("location_state", "value", s.value);
                                }
                            });
                            setTimeout(() => {
                                vm.$refs.dynamicForm.UpdateKeyValue();
                            }, 100);  
                        }
                        vm.$refs.dynamicForm.UpdateKeyValue();
                    } 
                }
                catch (ex) {
                }
            },
                updateSelectAll() {
                    var vm = this;
                    const totalItems = this.GetReviewQuestionList.length;
                    const selectedCount = this.selectedItems.length;
                    vm.selectAll = totalItems > 0 && selectedCount === totalItems;
                },
                selectAllItems() {
                    var vueObj = this;
                    this.selectAll = !this.selectAll;
                    if (this.selectAll) {
                        this.GetReviewQuestionList.forEach(function (item) {
                            vueObj.selectedItems.push(item.QUESTION_BANK_ID);
                        })
                    } else {
                        this.selectedItems = [];
                    }
                },
                GetRadioStarValue(index,value) {
                    this.GetReviewQuestionList[index].IS_STAR_ENABLE =  value;
                },
                GetRadioVisibilityValue(index,value) {
                    this.GetReviewQuestionList[index].VISIBLE_FOR =  value;
                },
                async GetReviewTemplateData(){
                    var vm = this;
                    var params = `template_Id=${vm.recordId}`
                    await DataService.GetReviewTemplateData(params).then(response=>{
                        var getResult = JSON.parse(response.data.Result);
                        if(getResult.length > 0)
                        {
                        vm.GetReviewQuestionList = getResult;
                        getResult.forEach(function (item) 
                        {
                            if (item.REVIEW_TEMPLATE_ID > 0) {
                                vm.selectedItems.push(item.QUESTION_BANK_ID);
                            }             
                        })
                        const totalItems = this.GetReviewQuestionList.length;
                        const selectedCount = this.selectedItems.length;
                        vm.selectAll = totalItems > 0 && selectedCount === totalItems;
                        }
                    }).catch(err=>{
                        console.log(err);
                    })
                },
            async Save() {
                var ths = this;
                var passportData = [];
                let fieldData = ths.$refs.dynamicForm.ConvertDynamicFormObjToCustomFieldObj(this.FormSchema)
                if (ths.moduleName == 'Company' && ths.subModule=='Break') {
                    fieldData.find(function (entry) {
                        if (entry.field_name == 'break_duration') {
                            entry.field_value = moment(new Date(entry.field_value)).format("HH:mm");
                        }
                    });
                } 
                if (ths.moduleName.toLowerCase() == 'company' && ths.subModule=='Shift') {                                
                    fieldData.find(function (entry) {
                        if (entry.field_name == 'start_time') {
                            entry.field_value = moment(new Date(entry.field_value)).format("HH:mm");
                        }
                    });
                } 
                if (ths.moduleName.toLowerCase() == 'company' && ths.subModule=='Shift') {                                  
                    fieldData.find(function (entry) {
                        if (entry.field_name == 'end_time') {
                            entry.field_value = moment(new Date(entry.field_value)).format("HH:mm");
                        }
                    });
                    fieldData.find(function (entry) {
                        if (entry.field_name == 'shift_type') {
                            entry.field_value = entry.field_value==true ? 1 : 0;
                        }
                    });
                } 
                if (ths.moduleName.toLowerCase() == 'company') {
                    var DurationValue = '';
                    var IsContract=false;
                    fieldData.find(function (entry) {
                            if (entry.field_name == 'IsForCompany') {
                                if(entry.field_value !=""){
                                    if(entry.field_value[0]=="0" && entry.field_value.length==1){
                                        IsContract= true;
                                        entry.field_value =  false;
                                    }else if(entry.field_value[0]=="1" && entry.field_value.length==1){
                                        entry.field_value =  true;
                                        IsContract= false;
                                    }else if(entry.field_value.length==2){
                                        entry.field_value =  true;
                                        IsContract= true;
                                    }
                                }else{
                                    entry.field_value =  entry.field_value;
                                }
                            }
                        });
                    fieldData.find(function (entry) {
                        if (entry.field_name == 'is_full_day') {
                            DurationValue = entry.field_value
                        }
                    });
                    if (DurationValue == "0") {
                        fieldData.find(function (entry) {
                            if (entry.field_name == 'from_time') {
                                entry.field_value = moment(new Date(entry.field_value)).format("HH:mm");
                            }
                        });
                        fieldData.find(function (entry) {
                            if (entry.field_name == 'to_time') {
                                entry.field_value = moment(new Date(entry.field_value)).format("HH:mm");
                            }
                        });
                    }
                        fieldData.find(function (entry) {
                            if (entry.field_name == 'IsForContract') {
                              entry.field_value =  IsContract;
                            }
                        });
                }
                if (ths.moduleName.toLowerCase() == 'company' && ths.subModule=='PaymentGateway') {   
                        var parameterData = '';
                        var fieldValues = fieldData.reduce((acc, item) => {
                            acc[item.field_name] = item.field_value;
                            return acc;
                        }, {});
                        var paymentCode = fieldValues['payment_gateway_code'];
                        var userName = fieldValues['user_name'];
                        var password = fieldValues['password'];
                        var signature = fieldValues['signature'];
                        if (paymentCode === "Authorize") {
                            parameterData = {
                                "APILoginId": userName,
                                "TransactionKey": password
                            };
                        } 
                        else if (paymentCode === "Stripe") {
                            parameterData = {
                                "SecretKey": userName,
                                "PublishableKey": password
                            };
                        } 
                        else if (paymentCode === "Paypal") {
                            parameterData = {
                                "UserName": userName,
                                "Password": password,
                                "Signature": signature
                            };
                        }
                        fieldData.find(function (entry) {
                            if (entry.field_name == 'parameters') {
                                entry.field_value = JSON.stringify(parameterData);
                            }
                        });
                } 
                
                if(ths.moduleName=='Configuration' && ths.subModule=='Passport'){
                    fieldData.find(function(entry){
                        if(entry.field_name != 'attachment'){
                            passportData.push(entry);
                        }
                    });
