                                    FeedbackQuestion: ths.$refs.dynamicForm.getPropertyValue("FeedbackQuestion" + group.index, "value"),
                                    FeedbackAnswerType:ths.$refs.dynamicForm.getPropertyValue("AnsweringType" + group.index, "value"),
                                    FeedbackAnsweringType: ths.FeedbackAnsweringValue.split('(Eg: ')[1].replaceAll(', ','_').replaceAll(')',''),
                                    FeedbackAnsweringValue:ths.$refs.dynamicForm.getPropertyValue("selectradiovalue" + group.index, "value")
                                }
                                feedbackQuestion.push(obj);
                            }
                    }); 
                    fieldData = {
                        data: fieldData,
                        feedbackData: feedbackQuestion
                    }
                }       
                else if(ths.moduleName == 'Review' && ths.subModule == 'PerformanceReview'){
                    let formData = [];
