                        if (this.selectedItems.includes(item.QUESTION_BANK_ID)) {
                            formData.push({
                                question_bank_id: item.QUESTION_BANK_ID,
                                is_star_enable: item.IS_STAR_ENABLE,
                                visible_for: item.VISIBLE_FOR,
                                review_template_id :item.REVIEW_TEMPLATE_ID,
                                review_template_question_id : item.REVIEW_TEMPLATE_QUESTION_ID
                            });
                        }
                    });
                    fieldData = {
                        data: fieldData,
                        questionDataList: formData
                    }
                }          
                ths.$refs.dynamicForm.validateForm.validate().then(result => {
                    if (result) {
                        ths.isLoading = true;
                        DataService.DepartmentAdd(fieldData, this.addApiUrl).then((res) => {
                        ths.isLoading = false;
