                            if (res.data.Status === 'AlreadyExists') {
                                ths.ShowAlert(ths.$t('AlreadyExists', true, [ths.$t(ths.subModule)]),'success',true,ths.$t('Alert'));
                            }
                            // if(res.data[0] !== undefined && res.data[0].Value.MSG=='DUPLICATE'){
                            //     ths.ShowAlert(ths.$t('AlreadyExists',true,[ths.$t(ths.subModule)]), 'success', true, ths.$t('Alert'));
                            // }
                            else{
                                ths.ShowAlert(ths.$t('AddedSuccess',true,[ths.$t(ths.subModule)]), 'success', true, ths.$t('Alert'));
                            }                            
                            if(ths.moduleName == 'Company' && ths.subModule == 'UserTitle')
                            {
