                                            group.Data.forEach((field, fi) => {
                                                if (field.name == "user_title_id") {
                                                    ths.callbackfunction('USER_TITLE', field, null);
                                                }
                                                
                                            });
                                        });
                                    }
                                
                            }
                            else if(ths.moduleName == 'Company' && ths.subModule == 'Department')
                            {
                                if (ths.$parent.FormSchema.length > 0) {
                                    ths.$parent.FormSchema.forEach((group, gi) => {
                                        group.Data.forEach((field, fi) => {
                                            if (field.name == "department_id") {
                                                ths.callbackfunction('DEPARTMENT', field, null);
                                            }                                                
                                        });
                                    });
                                }                                
                            }
                            else if(ths.moduleName == 'Company' && ths.subModule == 'Designation')
                            {                               
                                if (ths.$parent.FormSchema.length > 0) {
                                        ths.$parent.FormSchema.forEach((group, gi) => {
                                            group.Data.forEach((field, fi) => {
                                                if (field.name == "designation_id") {
                                                ths.callbackfunction('USER_DESIGNATION', field, null);
                                                }
                                            
                                            });
                                        });
                                    }
                                
                            }
                            else if(ths.moduleName == 'Company' && ths.subModule == 'Shift')
                            {                               
                                if (ths.$parent.FormSchema.length > 0) {
                                        ths.$parent.FormSchema.forEach((group, gi) => {
                                            group.Data.forEach((field, fi) => {
                                                if (field.name == "shift_id") {
                                                ths.callbackfunction('USER_SHIFT_WITH_LOCATION', field, null);
                                                }
                                            
                                            });
                                        });
                                    }
                                
                            }
                            this.closePopup();
                            this.$emit(this.emitName)
                        } 
                    else if(ths.moduleName == 'Company' && ths.subModule == 'LoginAuthorization') {          
                        if(res.data.Code ==-1){
                                ths.ShowAlert(ths.$t('ValidIP'), "warning", true, ths.$t("Alert"));
                            }
                        }
                        else {
                            if (res.data.Result != null && res.data.Result.MAIN_ID > 0 && res.data.Result.CHK_OPERATION =="UPDATE") {
                                // if(response.data[0].Value.MSG=='DUPLICATE'){
                                //     ths.ShowAlert(ths.$t('AlreadyExists',true,[ths.$t(ths.subModule)]), 'success', true, ths.$t('Alert'));
                                // }
                                ths.ShowAlert(ths.$t('UpdatedSuccess',true,[ths.$t(ths.subModule)]), 'success', true, ths.$t('Alert'));
                                this.closePopup();
                                this.$emit(this.emitName)
                        } 
                        else if(this.$t(res.data.Status) == 'FAILURE'){  
                                this.ShowAlert(this.$t('ErrorWhileSavingRecords'), "danger", false, this.$t("Alert"));
                            }
                            else{
                                this.ShowAlert(this.$t('RecordAlreadyExists'), "danger", false, this.$t("Alert"));
                            }
                        }
        
                    })
                    }
                });
            },
            closePopup() {               
                this.$emit('close') 
                this.$parent.showAddPopup=false;
                this.$parent.FetchData();   
            },
            GetRadioValue(data,event, getid){
                var vm = this;
                if(data != undefined){
                    if(data.includes('stars')){
                         var elements = document.getElementsByName(data);  
                         for (i=0;i<elements.length;i++) {                            
                            $('input[type="radio"]data').prop('checked',false);
                        }
                        $('input[type="radio"]#getid').prop('checked',true);
                    }
                }
                
                if(data.includes('radiovisibility')){
                         var elements = document.getElementsByName(data);  
                         for (i=0;i<elements.length;i++) {                            
                            $('input[type="radio"]data').prop('checked',false);
                        }
                        $('input[type="radio"]#getid').prop('checked',true);
                    }                            
            },
            SelectAllChk(data){
                if(data == 'ChkMainItems'){
                    var array = document.getElementsByTagName("input");  
                    for(var i = 0; i < array.length; i++){
                        if(array[i].type == "checkbox"){
                            if(array[i].className == 'deletable custom-control-input'){
                                var getScreenshotids = document.getElementsByClassName('masterchk custom-control-input ')
                                if(getScreenshotids[0].checked){
                                    array[i].checked = true;
                                }else{
                                    array[i].checked = false;
                                }
                            }
                        }
                    }
                }
            },
             AddMultiQuestionAnswer(e, x, y) {
                var vm = this;
                var obj = {
                    groupId:null,
                    group_name:"",
                    layoutType:'double',
                    visibility:true,
                    index: (vm.FormSchema.length - 1),
                    Data: [
                          {
                                  astype: "TextField",
                                  label: "Question Id",
                                  name: "QuestionId"+ (vm.FormSchema.length - 1),
                                  value: 0,
                                  chktxtIndex:(vm.FormSchema)[1].Data.length,
                                  visibility:false,
                        },
                        {
                            astype: "TextField",
                            label: "Feedback Question",
                            name: "FeedbackQuestion" + (vm.FormSchema.length - 1),
                            value: "",
                            placeholder: "",
                            disabled: false,
                            chktxtIndex: (vm.FormSchema)[1].Data.length,
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: "required",
                        },
                        {
                            astype: "SelectField",
                            label: "Answering Type",
                            name: "AnsweringType" + (vm.FormSchema.length - 1),
                            value: "",
                            placeholder: "",
                            disabled: false,
                            chktxtIndex: (vm.FormSchema)[1].Data.length,
                            config: {
                                options: [{ value: "Comments", name: "Comments" }, { value: "Star Rating", name: "Star Rating" },{ value: "Objective", name: "Objective" }],
                                showAddIcon: true,
                                onChange: vm.GetAnswerType,
                                onAddButtonClick: vm.DeleteFeedbackRow,
                                addIcon: 'fa fa-trash'
                            },
                            validationRules: "required",
                        },
                        {
                            astype: "RadioField",
                            label: "",
                            name: "selectradiovalue" + (vm.FormSchema.length - 1),
                            value: "2-point scale (Eg: Yes, No)",
                            placeholder: "",
                            visibility:false,
                            disabled: false,
                            chktxtIndex: (vm.FormSchema)[1].Data.length,
                            config: {                                              
                                options: [
                                    { value: "2-point scale (Eg: Yes, No)", name: "2-point scale (Eg: Yes, No)" }, 
                                    { value: "3-point scale (Eg: Agree, Undecided, Disagree)", name: "3-point scale (Eg: Agree, Undecided, Disagree)" },
                                    { value: "5-point scale (Eg: Poor, Bad, Neutral, Good, Very Good)", name: "5-point scale (Eg: Poor, Bad, Neutral, Good, Very Good)" }
                                ],
                            },
                            validationRules: "",
                        },

                    ] 
                    
                }
                vm.FormSchema.push(obj)
        },
            RemoveFile: function (e, field) {
            var vm = this;
            var checkindex = (vm.FormSchema)[1].Data.findIndex(x => x.chktxtIndex === field.chktxtIndex);
            var chechIndex1 = checkindex - 1;
            if (checkindex > -1) {
                (vm.FormSchema)[1].Data.splice(checkindex, 1);
            }
            if (chechIndex1 > -1) {
                (vm.FormSchema)[1].Data.splice(chechIndex1, 1);
            }
         },
            DeleteFeedbackRow(e, field){
                var ths = this;
                let index = -1
                ths.FormSchema.forEach(function(group, gi){
                    group.Data.forEach(function(item, fi){
                        if(item.name == field.name){
                            index = gi
                        }
                    });
                })
                ths.FormSchema.splice(index, 1);
            }, 
            fillAddress(item){  
                 if (this.isEditMode && !this.isEditModeInitialized) {
                        this.$refs.dynamicForm.updateProperty("googlemap","value", item.coordinates);
                        this.isEditModeInitialized = true;
                        return;
                }

                if(item != null && item != undefined){
                    this.$refs.dynamicForm.updateProperty("radius_meters", "value", item.radiusValue);
                    this.$refs.dynamicForm.updateProperty("location_pincode", "value", item.postalCode);
                    this.$refs.dynamicForm.updateProperty("googlemap","value", item.coordinates);
                    this.$refs.dynamicForm.updateProperty("latitude", "value", item.latitude);
                    this.$refs.dynamicForm.updateProperty("longitude", "value", item.longitude);
                    this.$refs.dynamicForm.updateProperty("location_address1", "value", item.addressLine1);
                    this.$refs.dynamicForm.updateProperty("location_address2", "value", item.addressLine2);
                    this.$refs.dynamicForm.updateProperty("location_city", "value", item.city);
                    this.$refs.dynamicForm.getPropertyValue("location_country", "config.options").forEach((c) => {
                        if (c.name == item.country) {
                            this.$refs.dynamicForm.updateProperty("location_country", "value", c.value);
                            this.GetStateDDL(c.value, item.state);
                        }
                    });                   
                    this.$refs.dynamicForm.UpdateKeyValue();
                    this.polygencoordinates =  item.coordinates ;
                    this.latitude = item.latitude;
                    this.longitude = item.longitude;
                }   
            },
        },
       
    
    });
</script>

