<template>
  <div class="location_gmap">

    <input id="autocomplete" placeholder="Enter a city" type="text" class="form-control padleft30" tabindex="17" />

    <!-- Map -->
    <div id="map" class="map-container" ></div>

    <!-- Address Details -->
   <!-- <div v-if="locationData" class="location-info">
      <h3> Location Info</h3>
      <p><strong>Latitude:</strong> {{ locationData.latitude }}</p>
      <p><strong>Longitude:</strong> {{ locationData.longitude }}</p>
      <p><strong>Address 1:</strong> {{ locationData.addressLine1 }}</p>
      <p><strong>Address 2:</strong> {{ locationData.addressLine2 }}</p>
      <p><strong>City:</strong> {{ locationData.city }}</p>
      <p><strong>Country:</strong> {{ locationData.country }}</p>
      <p><strong>State:</strong> {{ locationData.state }}</p>
      <p><strong>PinCode:</strong> {{ locationData.postalCode }}</p>
    </div>-->
  </div>
</template>

<script>
const keyforGoogle = process.env.VUE_APP_GOOGLE_API_KEY;
export default {
  name: "MapWithShapes",
  props: {
    geofenceEnabled: {
      type: String,
      default: false,
    },
    geofenceType: {
      type: String,
      required: false,
    },
    latitude: {
      type: Number,
      required: false,
    },
    longitude: {
      type: Number,
      required: false,
    },
    polygencoordinates: {
      type: String,
      required: false,
    },
    radiusValue: {
      type: String,
      required: false,
    },
  },
  watch:{
    geofenceEnabled(newVal) {
      this.reloadMap(newVal, this.geofenceType,this.radiusValue);
    },
    geofenceType(newVal) {
      this.reloadMap(this.geofenceEnabled, newVal,this.radiusValue);
    },
    radiusValue(newVal) {
      this.reloadMap(this.geofenceEnabled,this.geofenceType, newVal);
    },
      
  },
  data() {
    return {
      map: null,
      marker: null,
      center: { lat: 37.7749, lng: -122.4194 },
      locationData: null,
      circle: null,
      polygon: null,
      drawingManager: null,
      coordinates:[],
      googleapi: keyforGoogle
    };
  },
  mounted() {
    this.loadGoogleMaps().then(() => {
      this.initMap();
      this.initAutocomplete();
      //this.movePacContainer();
    });
  },
  methods: {
    loadGoogleMaps() {
      return new Promise((resolve, reject) => {
        if (window.google && window.google.maps && window.google.maps.drawing) {
          resolve();
          return;
        }
        const script = document.createElement("script");
        script.src =
          `https://maps.googleapis.com/maps/api/js?key=${this.googleapi}&libraries=places,drawing&language=en`;
        script.async = true;
        script.defer = true;

        script.onload = () => {
          if (window.google && window.google.maps && window.google.maps.drawing) {
            resolve();
          } else {
            reject("Google Maps Drawing Library failed to load.");
          }
        };

        script.onerror = reject;
        document.head.appendChild(script);
      });
    },

    initMap() {
      //debugger;
      if (window.google && window.google.maps) {
        if (this.latitude && this.longitude) {
          this.center = { lat: this.latitude, lng: this.longitude };
          
        } else {
          this.center = { lat: 37.7749, lng: -122.4194 }; // Default to San Francisco
        }
        this.map = new google.maps.Map(document.getElementById("map"), {
          center: this.center,
          zoom: 13,
        });
        if (this.latitude && this.longitude && this.geofenceEnabled === 'Yes') {
          this.placeLocator(this.center);
        }

      // Allow user to place a marker only if none exists and no geofence is active
        if (!this.marker && !this.circle && !this.polygon) {
          google.maps.event.addListener(this.map, 'click', (event) => {
            // Recheck inside event to avoid race conditions
            if (!this.marker && !this.circle && !this.polygon) {
              const clickedLocation = {
                lat: event.latLng.lat(),
                lng: event.latLng.lng(),
              };
              this.placeLocator(clickedLocation);
            }
          });
        }
       
        if (this.geofenceEnabled == 'Yes') {
          this.setupDrawingManager();
        }
        if ( this.geofenceEnabled === 'Yes'&& this.geofenceType === 'Draw' && this.polygencoordinates) {
          const coordinates = this.parseWKTPolygon(this.polygencoordinates);
          this.polygon = new google.maps.Polygon({
            paths: coordinates,
            strokeColor: "#FF0000",
            strokeOpacity: 0.8,
            strokeWeight: 2,
            fillColor: "#FF0000",
            fillOpacity: 0.35,
          });
          this.polygon.setMap(this.map);
          this.coordinates = coordinates;

          const bounds = new google.maps.LatLngBounds();
          coordinates.forEach(coord => bounds.extend(coord));
          this.map.fitBounds(bounds);
        }

        if (this.geofenceEnabled === 'Yes' && this.geofenceType === 'Radius' && parseFloat(this.radiusValue) > 0) {
          this.circle = new google.maps.Circle({
            strokeColor: "#FF0000",
            strokeOpacity: 0.8,
            strokeWeight: 2,
            fillColor: "#FF0000",
            fillOpacity: 0.35,
            map: this.map,
            center: this.center,
            radius: parseFloat(this.radiusValue), // meters
          });

          const bounds = this.circle.getBounds();
          this.map.fitBounds(bounds);
        }

      } else {
        console.error("Google Maps API failed to load.");
      }
    },
    initAutocomplete() {
      const input = document.getElementById("autocomplete");
      const autocomplete = new google.maps.places.Autocomplete(input, {
        types: [], // or use ['geocode'] for more general results
      });

      autocomplete.addListener("place_changed", () => {
        const place = autocomplete.getPlace();

        // if (!place.geometry || !place.geometry.location) {
        //   alert("No details available for input: '" + place.name + "'");
        //   return;
        // }

        const location = {
          lat: place.geometry.location.lat(),
          lng: place.geometry.location.lng(),
        };
        console.log("Selected location: ", location);
        this.map.setCenter(location);
        this.placeLocator(location);
      });
    },
    movePacContainer() {
  const input = document.getElementById("autocomplete");

  if (!input) {
    console.warn("Autocomplete input not found");
    return;
  }

  const observer = new MutationObserver(() => {
    const pac = document.querySelector('.pac-container');
    const modal = input.closest('.modal'); // Adjust this selector to match your modal

    if (pac && modal) {
      modal.appendChild(pac); // Move it into the modal
      observer.disconnect();
    }
  });

  observer.observe(document.body, { childList: true, subtree: true });
},


    reloadMap(isEnable, type,radiusValue,lat,lng) {
      this.geofenceEnabled = isEnable;
      this.geofenceType = type;
      this.radiusValue = radiusValue;


      // Clear existing map container
      const mapContainer = document.getElementById("map");
      if (mapContainer) {
        mapContainer.innerHTML = ""; // Clear map content
      }

      // Reset all overlays and markers
      this.marker = null;
      this.circle = null;
      this.polygon = null;
      this.drawingManager = null;
      this.map = null;
      this.loadGoogleMaps().then(() => {
        this.initMap();
        this.initAutocomplete();
        //this.movePacContainer();
      });
    },
    placeLocator(location) {
      if (this.marker) {
        this.marker.setMap(null);
      }

      this.marker = new google.maps.Marker({
        position: location,
        map: this.map,
        title: "Drag me!",
        draggable: true,
        animation: google.maps.Animation.DROP,
      });

      this.getAddressFromLatLng(location);

      this.marker.addListener("dragend", (event) => {
        const newPos = {
          lat: event.latLng.lat(),
          lng: event.latLng.lng(),
        };

        this.getAddressFromLatLng(newPos);
        this.map.setCenter(newPos);

        // Move circle with marker
        if (this.circle && this.geofenceType === 'Radius') {
          this.circle.setCenter(newPos);
        }

        // Move polygon with marker
        if (this.polygon && this.geofenceType === 'Draw') {
          const path = this.polygon.getPath();
          const oldCenter = this.getPolygonCenter(path);
          const latOffset = newPos.lat - oldCenter.lat();
          const lngOffset = newPos.lng - oldCenter.lng();

          const newPath = path.getArray().map((latLng) => ({
            lat: latLng.lat() + latOffset,
            lng: latLng.lng() + lngOffset,
          }));

          this.polygon.setPath(newPath);
        }

        this.fillAddressDetails(); // Emit updated location data
      });
    },
    getPolygonCenter(path) {
      let bounds = new google.maps.LatLngBounds();
      path.forEach((latLng) => bounds.extend(latLng));
      return bounds.getCenter();
    },
    getAddressFromLatLng(latLng) {
      const geocoder = new google.maps.Geocoder();

      geocoder.geocode({ location: latLng }, (results, status) => {
        if (status === "OK" && results[0]) {
          const address = results[0];
          const components = address.address_components;

         const getComponent = (type, useShortName = false) => {
            const comp = components.find((c) => c.types.includes(type));
            return comp ? (useShortName ? comp.short_name : comp.long_name) : "";
          };
          this.locationData = {
            latitude: latLng.lat,
            longitude: latLng.lng,
            addressLine1: address.formatted_address,
            addressLine2: getComponent("route"),
            city: getComponent("locality") || getComponent("administrative_area_level_2"),
            country: getComponent("country"),
            state: getComponent("administrative_area_level_1", true),
            postalCode: getComponent("postal_code"),
            coordinates: null,
            radiusValue: null,
            //markercoordinates: `${latLng.lat},${latLng.lng}`,
          };
          // Handle geofence-specific data
          if (this.geofenceType === "Radius" ) {
            if(this.circle){
              this.locationData.radiusValue = this.circle.getRadius();
            }
          } else if (this.geofenceType === "Draw" ) {
            if(this.polygon) {
              const path = this.polygon.getPath().getArray();
              const coordinates = path.map(latLng => ({
                lat: latLng.lat(),
                lng: latLng.lng(),
              }));
              this.coordinates = coordinates;
              this.locationData.coordinates = this.toGeoJSONPolygon(coordinates);
            }
          }

          this.fillAddressDetails()
        } else {
          console.error("Geocoder failed: ", status);
        }
      });
    },
    setupDrawingManager() {
      const drawingModes = [];
      if (this.geofenceEnabled) {
        if (this.geofenceType === 'Radius') {
          drawingModes.push(google.maps.drawing.OverlayType.CIRCLE);
        } else {
          drawingModes.push(google.maps.drawing.OverlayType.POLYGON);
        }
      }
      this.drawingManager = new google.maps.drawing.DrawingManager({
        drawingMode: null,
        drawingControl: true,
        drawingControlOptions: {
          position: google.maps.ControlPosition.RIGHT_TOP,
          drawingModes: drawingModes,
          // drawingModes: [
          //   google.maps.drawing.OverlayType.CIRCLE,
          //   google.maps.drawing.OverlayType.POLYGON,
          // ],
        },
        circleOptions: {
          fillColor: "#ffff00",
          fillOpacity: 0.4,
          strokeWeight: 1,
          clickable: false,
          editable: false,
          zIndex: 1
        },
        polygonOptions: {
          fillColor: "#ff0000",
          fillOpacity: 0.4,
          strokeWeight: 1,
          clickable: false,
          editable: true,
          zIndex: 1,
        },
      });

      this.drawingManager.setMap(this.map);
      // google.maps.event.addListener(this.drawingManager, "overlaycomplete", (event) => {
      //   if (event.type === google.maps.drawing.OverlayType.CIRCLE) {
      //     if (this.circle) 
      //     this.circle.setMap(null);
      //     this.circle = event.overlay;
          
      //   } else if (event.type === google.maps.drawing.OverlayType.POLYGON) {
      //     if (this.polygon) this.polygon.setMap(null);
      //     this.polygon = event.overlay;
      //     const path = this.polygon.getPath();
      //     const coordinates = [];
      //     path.forEach((latLng) => {
      //       coordinates.push({ lat: latLng.lat(), lng: latLng.lng() });
      //     });
      //     console.log("Polygon coordinates: ", coordinates);
      //     this.coordinates = coordinates;
      //     this.locationData.coordinates = this.toGeoJSONPolygon(coordinates);
      //     console.log("geo coordinates: ",  this.locationData.coordinates);
      //   }
      //   this.$emit("updatelocationData", this.locationData);
      //});

      google.maps.event.addListener(this.drawingManager, "overlaycomplete", (event) => {
        if (event.type === google.maps.drawing.OverlayType.CIRCLE) {
        if (this.circle) this.circle.setMap(null);
        this.circle = event.overlay;

        const center = this.circle.getCenter();
        const radiusInMeters = this.circle.getRadius(); // Radius in meters

        this.map.setCenter(center);
        this.map.setZoom(13);

        this.placeLocator({ lat: center.lat(), lng: center.lng() });

        const bounds = this.circle.getBounds();
        this.map.fitBounds(bounds);

        this.locationData = {
          ...this.locationData,
          latitude: center.lat(),
          longitude: center.lng(),
          radiusValue: radiusInMeters, //  Set the radius value
          coordinates: null,
        }
      } else if (event.type === google.maps.drawing.OverlayType.POLYGON) {
        if (this.polygon) this.polygon.setMap(null);
        this.polygon = event.overlay;

        const path = this.polygon.getPath();
        const coordinates = [];
        const bounds = new google.maps.LatLngBounds();

        path.forEach((latLng) => {
          const point = { lat: latLng.lat(), lng: latLng.lng() };
          coordinates.push(point);
          bounds.extend(latLng);
        });

        const center = bounds.getCenter();
        this.map.setCenter(center);
        this.map.fitBounds(bounds);

        this.placeLocator({ lat: center.lat(), lng: center.lng() });

        this.coordinates = coordinates;
        this.locationData = {
          ...this.locationData,
          latitude: center.lat(),
          longitude: center.lng(),
          coordinates: this.toGeoJSONPolygon(coordinates),
          radiusValue: null,
        };
      }

      this.$emit("updatelocationData", this.locationData);
    });
    },
    fillAddressDetails() {
      if (this.locationData) {
        // if (this.geofenceType === 'Radius') {
        //   this.locationData.coordinates = null;
        // } else if (this.geofenceType === 'Draw') {
        //   this.locationData.radiusValue = null;
        // }

      this.$emit("updatelocationData", this.locationData);
     }
     
    },
    toGeoJSONPolygon(coords) {
      const geoCoords = coords.map(p => [p.lng, p.lat]);

      // Ensure the polygon is closed
      const first = geoCoords[0];
      const last = geoCoords[geoCoords.length - 1];
      if (first[0] !== last[0] || first[1] !== last[1]) {
        geoCoords.push(first);
      }

      const geoJSON = {
        type: "Polygon",
        coordinates: [geoCoords]
      };

      return JSON.stringify(geoJSON);
    },
    parseWKTPolygon(wkt) {
      if (!wkt || !wkt.startsWith("POLYGON")) return [];

      const rawCoords = wkt
        .replace("POLYGON ((", "")
        .replace("))", "")
        .split(",");

      return rawCoords.map(pair => {
        const [lng, lat] = pair.trim().split(" ").map(Number);
        return { lat, lng };
      });
    }

  },
};
</script>

